/* aside bar стили */

aside {
    margin-top: 40px;
    width: 140px;
    display: flex;
    flex-direction: column;
}

.logo {
    text-align: center;
}

.aside-menu {
    display: flex;
    flex-direction: column;

    &__item {
        display: flex;
        flex-direction: row;
        align-items: center;
        margin-bottom: 31px;
    
        &:hover {border-right: 3px solid $link-color;}
    }

    &__img {
        margin-right: 15px;
    }

    &__title {
        color: $link-color;
        font-style: italic;
    }
}

.tweets {
    display: flex;
    flex-direction: column;
}

.tweet-block {
    display: flex;
    flex-direction: column;

    &__title {
        margin-top: 5px;
        font-size: 12px;
        font-family: Verdana;
        text-transform: uppercase;
        border-bottom: 1px dashed #E5E5E5;
    }

    &__text {
        margin-top: 10px;
        font-size: 10px;
        font-style: italic;
        line-height: 19px;
    }
}

.tweet-btn {
    display: flex;
    flex-direction: row;
    background: #FFFFFF;
    margin-top: 10px;
    align-items: center;
    color: $link-color;
    border: 1px solid #E5E5E5;
    font-size: 10px;
    font-style: italic;
    border-radius: 5px;
    padding: 7px 0px 7px 7px;

    &:hover {
        border-color: $link-color;
    }

    &:active {
        border-color: rgb(17, 240, 229);
    }

    &__img {
        margin-right: 5px;
    }

}
